/* Fonts */

// @include font("Authentic", "../fonts/AUTHENTICSans-60", $weight: light);
@include font("Authentic", "../fonts/AUTHENTICSans-90", $weight: regular);
@include font("Authentic", "../fonts/AUTHENTICSans-150", $weight: bold);
// @include font("Authentic-condensed", "../fonts/AUTHENTICSans-Condensed-60", $weight: light);
// @include font("Authentic-condensed", "../fonts/AUTHENTICSans-Condensed-90", $weight: regular);
// @include font("Authentic-condensed", "../fonts/AUTHENTICSans-Condensed-130", $weight: bold);

@mixin font-authentic( $weight: regular) {
	font-family: Authentic, sans-serif;
	font-weight: $weight;
}

// @mixin font-authentic-condensed ( $weight: regular) {
// 	font-family: Authentic-condensed, sans-serif;
// 	font-weight: $weight;
// }
 
.font-authentic {
	@include font-authentic();
}

